import { IPlayerView } from "../../player/interfaces";
import { ScorePlayer } from "../models";

export class ScorePlayerService {
    
    public getData(): ScorePlayer[] {
        return [
            {
                username: 'Playe 1',
                avatar: '',
                score: 1231
            },
            {
                username: 'Playe 2',
                avatar: '',
                score: 32432
            },
            {
                username: 'Playe 3',
                avatar: '',
                score: 22
            }
        ];
    }

    public getDataByUserName(valueToSearch: string): ScorePlayer[] {
        return this.getData().filter((sp: ScorePlayer) => sp.username.indexOf(valueToSearch) > -1)
    }
    public removePlayer(valueToRemove: string): ScorePlayer[] {
        return this.getData().filter(function(ele){
            return ele.username !== valueToRemove;
            
        });
    }
   
}