import { ScorePlayer } from "../models/models";
import { ScorePlayerService } from "../models/services/scoreplayer.service";
import { PlayerView } from "../player/player.view";
import { IScorePresenter, IScoreView } from "./interfaces";


export class ScorePresenter implements IScorePresenter {
    private scorePlayerSevice = new ScorePlayerService();

    constructor(private view: IScoreView) {
        this.initialize();
    }

    onSearchChanged(searchValue: string): void {
        console.log("Search on Presener", this);
        const scoreData: any = this.scorePlayerSevice.getDataByUserName(searchValue);
        const scoreViews = scoreData.map((s: ScorePlayer) => new PlayerView(s));
        this.view.renderScore(scoreViews);
    }

    initialize() {
        const scoreData: any = this.scorePlayerSevice.getData();
        const scoreViews = scoreData.map((s: ScorePlayer) => new PlayerView(s));
        this.view.renderScore(scoreViews);
    }
}